<template>
    <div class='container'>
        <div class='control'>
            <div class='control-camera'>
                <div :class='{"selected": cameraType === "free"}' @click='changeCameraType("free")'>自由</div>
                <div :class='{"selected": cameraType === "top"}' @click='changeCameraType("top")'>俯视</div>
                <div :class='{"selected": cameraType === "side"}' @click='changeCameraType("side")'>正视</div>
            </div>
        </div>
        <div ref='stats'></div>
        <div ref='canvas' id='WebGL-output'></div>
    </div>
</template>

<script>
import math from '../mixins/math.js';
import mixin from '../mixins/threeMixin.js';
import loadModel from '../mixins/loadModel.js';
import P from '../mixins/params.js';

const { PI, cos, sin, floor } = Math;
const { THREE, TWEEN } = window;
const mainGroup = new THREE.Group();
mainGroup.name = 'mainGroup';
const roadGroup = new THREE.Group();
const tunnelGroup = new THREE.Group();
export default {
    mixins: [mixin, math, loadModel],
    data() {
        return {
            prevTarget: null,
            cameraType: 'free',
            controlSwitch: true,
            cameraPosition: {
                free: { x: 560, y: 17, z: 1174 },
                top: { x: 0, y: 3000, z: 0 },
                side: { x: 1000, y: 0, z: -800 },
            },
        };
    },
    methods: {
        changeCameraType(type) {
            switch (type) {
                case 'free':
                    this.controlSwitch = true;
                    this.cameraControl.lookAt(100, 0, 0);
                    console.log(this.cameraControl);
                    break;
                case 'top':
                    this.controlSwitch = false;
                    break;
                case 'side':
                    this.controlSwitch = false;
                    break;
                default:
                    return;
            }
            const { x, y, z } = this.cameraPosition[type];
            this.camera.position.set(x, y, z);
            this.camera.lookAt(0, 0, 0);
            this.cameraType = type;
        },
        initWebGL() {
            const { scene, camera, renderer } = this.initBasics(this.$refs.canvas, {
                position: { x: 560, y: 17, z: 1174 }, antialias: true
            }, {
                clearColor: 0x1F243A, shadowEnabled: true
            });
            this.scene = scene;
            this.camera = camera;
            this.renderer = renderer;
            this.scene.add(mainGroup);
            mainGroup.add(tunnelGroup);
            tunnelGroup.add(roadGroup);

            // this.addAxes(1000);
            // const stats = this.initStats(this.$refs.stats);

            const control = this.addControl();
            this.cameraControl = control;
            const clock = new THREE.Clock();

            this.addLight();
            this.addModel();

            const renderScene = () => {
                // stats.update();
                // console.log(this.camera.position);
                TWEEN.update();
                if (this.controlSwitch) {
                    const delta = clock.getDelta();
                    control.update(delta);
                }
                requestAnimationFrame(renderScene);
                renderer.render(scene, camera);
            };
            renderScene();
        },
        addModel() {
            const tunnel = this.loadModel('suidao');
            tunnel.then((obj) => {
                mainGroup.add(obj.children[0]);
                // const geom = new THREE.Geometry().fromBufferGeometry(obj.children[0].geometry);
                // geom.mergeVertices();
                // const testPoints = this.addTestPoints(geom.vertices);
                // mainGroup.add(testPoints);
                // this.linstenClick(testPoints.children);
            });
        },
        addTestPoints(vertices) {
            const pointsGroup = new THREE.Group();
            const material = new THREE.SpriteMaterial({ color: 0xDC143C });
            const points = vertices.forEach((v, i) => {
                if (i > 2000) return;
                // if (i % 126 === 0) {
                    const sprite = new THREE.Sprite(material);
                    sprite.position.set(v.x, v.y, v.z);
                    sprite.index = i;
                    sprite.scale.set(0.2, 0.2, 0.2);
                    pointsGroup.add(sprite);
                // }
            });
            return pointsGroup;
        },
        addLight() {
            const ambientLight = this.initLight('Ambient', {color: 0xffffff});
            const target = this.initTarget(0, 0, 0);
            const directionalLight = this.initLight('Directional', {
                color: 0x919191,
                position: {x: 0, y: 50, z: 0},
                castShadow: true,
                shadow: {
                    camera: {
                        near: 2,
                        far: 100,
                        left: -650,
                        right: 650,
                        top: 1300,
                        bottom: -1300,
                    },
                    mapSize: {
                        width: 2048,
                        height: 2048,
                    },
                },
            });
            directionalLight.target = target;
            // const helper = new THREE.CameraHelper(directionalLight.shadow.camera);
            // this.scene.add(helper);
            mainGroup.add(ambientLight);
            mainGroup.add(directionalLight);
        },
        linstenClick(objList) {
            this.listenEvent('click', this.camera, objList, (targetList) => {
                if (!targetList.length) return;
                const target = targetList[0];
                console.log(target);
                if (this.prevTarget) {
                    const distance = this.distance(target.point, this.prevTarget.point);
                    console.log(distance);
                } else {
                    this.prevTarget = target;
                }
            });
        },
        addAxes(len) { // 默认辅助坐标系
            const axes = this.initAxes(len);
            this.scene.add(axes);
        },
        addControl() { // 摄影机控制器
            const control = this.initControls('FirstPerson', this.camera, this.renderer.domElement, {
                lookSpeed: 0.1,
                movementSpeed: 20,
                noFly: true,
                lookVertical: true,
                constrainVertical: true,
                verticalMin: 1.0,
                verticalMax: 2.0,
                lon: -50,
                lat: 50,
            });
            return control;
        },
    },
    mounted() {
        this.initWebGL();
    },
}
</script>

<style lang='less' scoped>
.control {
    position: fixed;
    top: 0;
    left: 0;
    &-camera {
        display: flex;
        width: 2.5rem;
        height: 0.4rem;
        margin: .1rem 0 0 .1rem;
        overflow: hidden;
        div {
            display: flex;
            flex-grow: 1;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            border: 1px solid #d9d9d9;
            background: #fff;
            &:first-child {
                border-radius: 5px 0 0 5px;
            }
            &:last-child {
                border-radius: 0 5px 5px 0;
            }
        }
        .selected {
            color: #1890ff;
            border-color: #1890ff;
        }
    }
}
</style>